:root {
  --Limegreen:hsl(163, 72%, 41%);
  --Brightred:hsl(356, 69%, 56%);
  --Facebook-color: hsl(208, 92%, 53%);
  --Twitter-color:hsl(203, 89%, 53%);
  --Instagram-color: linear-gradient(hsl(37, 97%, 70%) to hsl(329, 70%, 58%));
  --YouTube-color: hsl(348, 97%, 39%);
  --Toggle-BG-dark: linear-gradient(hsl(210, 78%, 56%) to hsl(146, 68%, 55%)) ;
  --Toggle-BG-light: hsl(230, 22%, 74%) ;
  --Very-Dark-Blue-BG: hsl(231, 19%, 14%);
  --Very-Dark-Blue-TopBG: hsl(232, 19%, 15%);
  --Dark-Desaturated-Blue-CardBG: hsl(228, 28%, 20%);
  --Desaturated-Blue-Text1: hsl(228, 34%, 66%);
  --Dark-Text2: hsl(0, 0%, 100%);
  --Light-BG: hsl(0, 0%, 100%);
  --Light-Top-BG: hsl(225, 100%, 98%);
  --Light-Grayish-Blue-Card-BG: hsl(227, 47%, 96%);
  --Light-Text1: hsl(228, 12%, 44%);
  --Light-Text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter:"Inter",sans,serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  background-color: aliceblue;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
}
.card {
  padding: 0.625rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  background-color: var(--Toggle-BG-light);
  border-radius: 50%;
}